.container {
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  padding: 0 30px;
  max-width: 800px;
  width: 100%;
}

h1 {
  color: #fffd;
  text-shadow:
    0 2px 2px #000,
    0 4px 4px #000a,
    0 6px 6px #0001;
  margin: 20px;
  text-align: center;
  font-size: 2rem;
  font-family: 'sans-serif';
}

h2 {
  color: #fffd;
  margin-bottom: 1rem;
}

h3 {
  color: #555;
  margin-bottom: 20px;
  font-size: 1.5rem;
}

.config-section {
  background: #f8f9fa;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 30px;
  border: 2px solid #e9ecef;
}

.config-section.success {
  background: #d4edda;
  border-color: #c3e6cb;
  color: #155724;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.config-instructions {
  color: #666;
  font-size: 14px;
  margin-bottom: 15px;
  font-style: italic;
}

.config-inputs {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.config-input {
  padding: 12px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  width: 100%;
  transition: border-color 0.3s;
}

.config-input:focus {
  outline: none;
  border-color: #667eea;
}

.input-section {
  display: flex;
  background: white;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  gap: 5px;
  margin-bottom: 3px;
}

.item-input {
  flex: 1;
  padding: 15px;
  border: 2px solid #ddd;
  border-radius: 10px;
  font-size: 16px;
  transition: border-color 0.3s;
}

.item-input:focus {
  outline: none;
  border-color: #667eea;
}

.btn {
  /* padding: 15px 30px; */
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  /* transition: all 0.3s; */
  white-space: nowrap;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.button-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  animation: spin 0.6s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.btn-secondary {
  background: #6c757d;
  color: white;
}

.btn-secondary:hover {
  background: #5a6268;
}

#btn-delete {
  background: #f345;
  border: 1px solid red;
  border-radius: 8px;
  color: white;
  padding: 4px 6px 0 6px;
  font-size: 14px;
}

.cross-btn {
  border: 0;
  font-size: 14px;
  background: transparent;
  position: absolute;
  top: -2px;
  right: -2px;
  padding: 2px 3.5px;
  border: 2px solid #f5c6cb;
  border-radius: 8px;
}

.loading {
  text-align: center;
  padding: 20px;
  color: #667eea;
  font-weight: 600;
}

.date-display {
  font-size: 16px;
  font-weight: 600;
  color: #667eea;
  margin-top: 10px;
}

.tasks-section {
  margin-bottom: 30px;
}

.tasks-list {
  display: grid;
  grid-row: auto;
  gap: 3px;
}

.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  gap: 10px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 2px solid #e9ecef;
}

.task-item:hover {
  transform: scale(1.02);
}

.task-item.completed {
  background: #d4edda;
  border-color: #c3e6cb;
}

.task-checkbox {
  width: 20px;
  height: 20px;
  padding: 10px;
  cursor: pointer;
  accent-color: #667eea;
}

.task-text {
  flex: 1;
  cursor: pointer;
  font-size: 16px;
  color: #333;
  word-break: break-word;
}

.task-item.completed .task-text {
  text-decoration: line-through;
  color: #6c757d;
}

.task-date {
  font-size: 12px;
  padding: 3px;
  border-radius: 8px;
  color: #6c757d;
  background: #e9ecef;
}

.summary-section {
  /* padding: 20px; */
  background: #f8f9fa;
  border-radius: 10px;
  border: 2px solid #e9ecef;
}

.summary-textarea {
  width: 100%;
  padding: 15px;
  border: 2px solid #ddd;
  border-radius: 10px;
  font-size: 16px;
  font-family: inherit;
  resize: vertical;
  /* margin-bottom: 15px; */
  transition: border-color 0.3s;
  min-height: 120px;
}

.summary-textarea:focus {
  outline: none;
  border-color: #667eea;
}

.summary-textarea:disabled {
  background: #e9ecef;
  cursor: not-allowed;
}

#empty-list {
  background: whitesmoke;
  border: 2px solid #e9ecef;
  border-radius: 10px;
  text-align: center;
  padding: 40px;
  color: #6c757d;
  font-size: 18px;
}

#btn {
  width: auto;
}

@media (max-width: 600px) {
  .container {
    padding: 20px;
  }

  h1 {
    font-size: 2rem;
  }

  .input-section {
    flex-direction: column;
  }

  .task-content {
    width: 100%;
  }
}
